/* -------------------------

Title: main navigation
Descr: nav--main small and large, hovers and toggles for subNav
       
Still needs: expand toggle for showing main nav on mobile

--------------------------- */
.nav,
.nav-1,
.nav-2,
.nav-3 {
  list-style-type: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
  font-size: var(--base-20);
  position: absolute;
  display: flex;
  flex-direction: column;
  background: var(--as-white);
  border-radius: var(--border-radius);
  width: 100%;
  margin-top: 1rem;
  z-index: 11;
  transform: scale(1, 0);
  transform-origin: top;
  transition: transform 150ms ease-out;

  //shown state for navs
  &.shown {
    transform: scale(1, 1);
    transform-origin: top;
    transition: transform 250ms ease-in-out 250ms;
    .mainNav__link {
      opacity: 1;
      transition: opacity 250ms ease-in-out 250ms;
    }
  }
  button {
    width: 1rem;
    right: 0px;
    top: 5px;
    padding: 0;
    background: none;
    border: none;
    position: absolute;
    z-index: 10;
    svg {
      fill: red;
      @include headerBreak() {
        fill: var(--as-black);
      }
    }
    &:focus {
      opacity: 1;
    }
    &.rotated {
      transform: rotate(180deg);
      transform-origin: 50% 50%;
    }
  }
  > .nav__item {
    background: var(--as-teal-400);
    margin-bottom: 0;
    // max-width: 100%;
    position: relative;
  }
  .nav__link {
    display: inline-block;
    position: relative;
    color: var(--as-gray-300);
    font-weight: var(--light);
    background: var(--as-black);
    padding: 0.5rem;
    border-right: 1px solid var(--as-white);
    width: calc(100% - 2.1rem);
    transition: opacity 150ms ease-in-out;
  }
}

// universal .nav-1

.nav-1 {
  background: var(--as-teal-300);
  font-weight: var(--regular);
  // display: flex;
  // flex-flow: column;
  width: 100%;
  padding-left: 0;
  transform: scaleY(0);
  transition: transform 150ms ease-in-out;
  transform-origin: top;
  &.expanded {
    opacity: 1;
    transform: scaleY(1);
    transition: all 250ms ease-in-out 150ms;
  }
  a {
    padding: 0.5rem 0 0.5rem 1rem;
    color: var(--as-black);
    text-decoration: underline;
    display: block;
    &:hover,
    &:focus {
      background-color: var(--as-white);
    }
  }
}

/*
.nav--main
*/

.nav--main {
  @include medium() {
    // display: flex;
    flex-direction: row;
    transform: none;
    width: 100%;
    color: var(--as-black);
    background: transparent;
    overflow: visible;
    .nav__item {
      background: transparent;
      // position: relative;
      // top bar thing
      &:after {
        position: absolute;
        content: "";
        width: calc(100% - 27px);
        height: 3px;
        background: var(--as-red-400);
        top: 0;
        left: 0;
        transform: scaleX(0);
        transition: all 0.15s ease-in;
        visibility: hidden;
        border-radius: 2px;
      }
      .nav__link {
        background: none;
        opacity: 1;
        color: var(--as-black);
        width: auto;
        font-weight: var(--semi);
      }
      // hover over main nav items
      &:hover,
      &.expanded {
        // border-top: 4px solid var(--as-red-400);
        text-decoration-color: var(--as-gray-500);
        &:after {
          visibility: visible;
          transform: scaleX(1);
          transition: all 0.25s ease-out;
        }
        overflow: visible;

        > .nav__link {
          box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.1);
        }
        .nav-1 {
          transform: scaleY(1);
        }
      }
    }
    .nav-1 {
      margin-top: 0;
      width: 260px;
      background: var(--as-white);
      position: absolute;
      font-size: var(--base-18);
      box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.1);
      border-radius: 0 var(--border-radius) var(--border-radius)
        var(--border-radius);
      overflow: visible;
      margin-left: 0;
      .nav-1__link {
        position: relative;
        &:hover {
          color: var(--as-white);
          background: var(--as-red-400);
          &:after,
          &:before {
            position: absolute;
            content: "";
            width: 0.25rem;
            height: 100%;
            top: 0;
            background: var(--as-red-400);
            border-radius: 4px;
          }
          &:after {
            right: -0.125rem;
          }
          &:before {
            left: -0.125rem;
          }
        }
      }
      &.expanded {
      }
    }
    button {
      opacity: 0;
    }
  }
}
